<template>
	<div id="member-delete">
		<public-header title="注销账户"></public-header>
		<div class="content">
			<div class="text-center">
				<span class="icon icon-infofill"></span>
			</div>
			<div class="font-16 font-bold text-center">将{{member.mobile}}所绑定的账号注销</div>
			<div class="font-12 c-gray text-center margin-15-tb">注意，注销账号后以下信息将被清空且无法找回</div>
			<div class="tips-group ">
				<div class="tips-item">身份、账户信息、会员及权益信息</div>
				<div class="tips-item">交易记录</div>
				<div class="tips-desc van-hairline--top">请确保所有交易已完结且无纠纷，账户删除后的历史交易可能产生的资金回退权益等将视作自动放弃</div>
			</div>
			<div class="margin-10-lr margin-15-tb">
				<van-button color="#ff2d4b" size="large" block @click="onDelete">注销</van-button>
			</div>
			<div class="tips-agreement">
				点击注销即代表你已同意
				<span class="c-danger" @click="onTogglePopup">《用户注销协议》</span>
			</div>
		</div>
		<agreement :show="agreementPopup" title="用户注销协议" :content="agreement" @agreementHide="onTogglePopup"></agreement>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import Agreement from '@/components/agreement'
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				member: {},
				agreement: '',
				agreementPopup: false,
				showPreLoading: true
			}
		},
		components: {
			PublicHeader, Agreement
		},
		methods: {
			onLoad() {
				var that = this;
				if(!that.util.isGlala()) {
					that.$router.replace(that.util.getUrl({path: '/pages/member/profile'}));
					return false;
				}
				getData({
					vue: that,
					url: 'wmall/member/profile/delete',
					data: {},
					success: function(res) {
						that.member = that.util.extend(that.member, res.member);
						that.agreement = res.agreement;
					}
				})
			},

			onDelete() {
				var that = this;
				that.$dialog.confirm({
					message: '确定注销' + that.member.mobile + '所绑定的账号吗',
					confirmButtonColor: '#ff2d4b'
				}).then(() => {
					setData({
						vue: that,
						url: 'wmall/member/profile/delete',
						data: {},
						success: (res) => {
							that.util.removeStorage('itoken');
							that.util.delCookie('itoken');
							that.util.$toast(res, that.util.getUrl({path: '/pages/auth/login'}), 1500, 'replace')
						}
					})
				}).catch(() => {})
			},

			onTogglePopup() {
				this.agreementPopup = !this.agreementPopup;
			}

		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#member-delete .icon-infofill{
		margin: 20px 0;
		font-size: 80px;
		color: #ff2d4b;
	}
	#member-delete .tips-group{
		margin: 10px;
		border-radius: 10px;
		background-color: #fff;
		font-size: 14px;
		padding: 15px;
		color: #7a7a7a;
	}
	#member-delete .tips-group .tips-item{
		position: relative;
		padding-left: 15px;
		margin-bottom: 15px;
	}
	#member-delete .tips-group .tips-item:before{
		content: '';
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		width: 5px;
		height: 5px;
		border-radius: 50%;
		background-color: #5f5f5f;
	}
	#member-delete .tips-desc{
		padding-top: 12px;
		line-height: 1.5;
	}
	#member-delete .tips-agreement{
		text-align: center;
		font-size: 14px;
		color: #7a7a7a;
	}
</style>


